import React from 'react'
import "./Filters.css"
import card1 from "./card1.png"
import card2 from "./card2.png"
import card3 from "./card3.png"
import card4 from "./card4.png"
import FilterCard from "../FilterCard/FilterCard";

const Filters = () => {
    return (
        <div className="filter-section wrapper" id="articles">
            <h1 className="filters-header">  </h1>
            <div className="filters">
                <div className="filters-elements">

                </div>
                <div className="filters-results-layout">
                    <FilterCard label={" "} withArrow={true} imgSrc={card1}/>
                    <FilterCard label={" "} imgSrc={card2}/>
                    <div className={"filters-results-layout filters-results-layout-vertical"}>
                        <div className={"vertical-card"}>
                            <FilterCard label={""} imgSrc={card3}/>
                        </div>
                        <div className={"vertical-card"}>
                            <FilterCard label={" "} imgSrc={card4}/>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Filters;