import React from 'react'
import "./Tests.css"
import TestButtonCard from "../TestButtonCard/TestButtonCard";
import clock from './clock.svg'
import question from './question.svg'

const Tests = () => {
    return (
        <div className="tests-section wrapper" id="tests">
            <h1 className="blue-header">   </h1>
            <div className="tests">
                <div className="test-row">
                    <TestButtonCard label={"  "} width={510} link={"#"}/>
                    <TestButtonCard label={" "} width={690} link={"#"}/>
                </div>
                <div className="test-row">
                    <TestButtonCard label={""} width={408} link={"#"}/>
                    <TestButtonCard label={" "} width={768} link={"#"}/>
                </div>
                <div className="test-row">
                    <TestButtonCard label={" "} width={588} link={"#"}/>
                    <TestButtonCard label={" "} width={588} link={"#"}/>
                </div>
            </div>
            <div className="test-info">
                <div className={"info-tab"}>
                    <img src={clock} alt="Clock"/>
                    <div className={"info-tab-text"}>
                        <div className={"text-header"}>10 </div>
                        <div className={"text-general"}></div>
                    </div>
                </div>

                <div className={"info-tab"}>
                    <img src={question} alt="Question"/>
                    <div className={"info-tab-text"}>
                        <div className={"text-header"}>4 </div>
                        <div className={"text-general"}></div>
                    </div>
                </div>
            </div>


            <a href="#" className={"start-test-btn"}> </a>
        </div>
    )
}

export default Tests;